import React, { useEffect, useState } from "react";

import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import Divider from "@mui/material/Divider";

import { getProject, clearProject } from "../redux/features/projectSlice";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";

import "react-quill/dist/quill.snow.css";
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css"; //
const ProjectCardDetail = () => {
  const [isLightboxOpen, setIsLightboxOpen] = useState(false);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  function handleLightboxOpen(index) {
    setIsLightboxOpen(true);
    setCurrentImageIndex(index);
  }

  function handleLightboxClose() {
    setIsLightboxOpen(false);
  }

  const dispatch = useDispatch();
  const { Project } = useSelector((state) => ({
    ...state.project,
  }));
  const { id } = useParams();

  useEffect(() => {
    if (id) {
      dispatch(getProject(id));
    }

    return () => {
      // Clear the project data when unmounting the component
      dispatch(clearProject());
    };
  }, [dispatch, id]);

  const Item = styled(Paper)(({ theme }) => ({
    background: "rgba(255, 255, 255, 0.2)",
    borderRadius: "16px",
    boxShadow: "0 4px 30px rgba(0, 0, 0, 0.1)",
    backdropFilter: "blur(5px)",
    WebkitBackdropFilter: "blur(5px)",
    border: "1px solid rgba(255, 255, 255, 0.3)",
  }));
  const imgStyle = {
    maxWidth: "100%",
    maxHeight: "80%",
    cursor: "pointer",
  };
  const imagesArray = [
    Project.imageFile2, // The first image shown in the lightbox
    Project.imageFile, // Second image
    Project.imageFile1, // Third image
    // Add more images here if needed
  ];
  return (
    <>
      <div style={{ marginTop: "100px" }}>
        <div className="About3">
          <h1>
            {" "}
            <a href={Project.link} className="link" target="site">
              Visit this site
            </a>{" "}
          </h1>{" "}
        </div>
        <Grid
          item
          lg={12}
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            textAlign: "center",
            marginTop: "40px",
          }}
        >
          <Item
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              textAlign: "center",
              width: "97%",
              padding: "20px",
            }}
          >
            <div className="ProjectTop">
              <div
                style={{
                  width: "70%",
                  justifyContent: "center",
                  textAlign: "center",
                  alignItems: "center",
                  display: "flex",
                  cursor: "pointer",
                }}
                data-aos="zoom-in"
                data-aos-delay="200"
                data-aos-duration="500"
              >
                <img
                  src={Project.imageFile}
                  alt="Image2"
                  style={{
                    width: "100%",
                    height: "100%",
                    border: "2px solid white",
                    borderRadius: "20px",
                    overflow: "hidden",
                  }}
                  onClick={() => handleLightboxOpen(1)} // Pass index 1 for Project.imageFile
                />
              </div>
              <br />
              <div
                style={{
                  width: "70%",
                  position: "relative",
                  right: "10%",
                  justifyContent: "center",
                  textAlign: "center",
                  alignItems: "center",
                  display: "flex",
                }}
                className="ProjectTextSm"
              >
                <span
                  className="ProjectTopTextDiv"
                  data-aos="zoom-in"
                  data-aos-delay="300"
                  data-aos-duration="500"
                >
                  <div className="About3">
                    {" "}
                    <h1 style={{ fontSize: "27px" }}> {Project.title} </h1>
                  </div>

                  {Project.toptext1}
                </span>
              </div>
            </div>
          </Item>
          <br />
          <br />
        </Grid>
        <br />
        <Grid
          item
          lg={12}
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            textAlign: "center",
            marginTop: "40px",
          }}
        >
          <Item
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              textAlign: "center",
              width: "97%",
              padding: "20px",
            }}
          >
            <div className="ProjectTop">
              <div
                style={{
                  width: "70%",
                  position: "relative",
                  left: "10%",
                  justifyContent: "center",
                  textAlign: "center",
                  alignItems: "center",
                  display: "flex",
                  zIndex: "1",
                }}
              >
                <span
                  className="ProjectTopTextDiv"
                  data-aos="zoom-in"
                  data-aos-delay="400"
                  data-aos-duration="500"
                >
                  {Project.toptext2}
                  <div className="About3">
                    {" "}
                    <h1 style={{ fontSize: "27px" }}>
                      {" "}
                      <a href={Project.link} className="link" target="site">
                        Visit this site
                      </a>{" "}
                    </h1>
                  </div>
                </span>
              </div>

              <br />

              <div
                style={{
                  width: "70%",
                  justifyContent: "center",
                  textAlign: "center",
                  alignItems: "center",
                  display: "flex",
                }}
                data-aos="zoom-in"
                data-aos-delay="500"
                data-aos-duration="500"
              >
                <img
                  src={Project.imageFile1}
                  alt="Hello World!"
                  className="modal-image"
                  onClick={() => handleLightboxOpen(2)} // Pass index 2 for Project.imageFile1
                  style={{
                    maxWidth: "100%",
                    maxHeight: "100%",
                    cursor: "pointer",
                    zIndex: "-1",
                  }}
                />
              </div>
            </div>
          </Item>
          <br />
          <br />
        </Grid>
        <br />
        <br />
        <br />
        <Grid
          container
          spacing={1}
          style={{ display: "flex", justifyContent: "center" }}
        >
          <br />
          <br />
          <br />

          <Grid item xs={11} style={{ overflow: "visible" }}>
            <Item className="moreproject">
              <div className="QuillDescription">
                <h1> More About this Project </h1>
                <div
                  className="PrjectsDescription QuillDescription"
                  data-aos="zoom-in"
                  data-aos-delay="300"
                  data-aos-duration="500"
                >
                  <p
                    dangerouslySetInnerHTML={{ __html: Project.description }}
                  />
                </div>
              </div>

              <br />

              <Divider />
              <br />
              <div
                style={{
                  display: "flex",
                  justifyContent: "center",
                  overflow: "hidden",
                }}
              >
                <div className="projectImg">
                  <div>
                    <img
                      src={Project.imageFile2}
                      alt="Hello World!"
                      className="modal-image"
                      onClick={() => handleLightboxOpen(0)}
                      style={imgStyle}
                    />
                  </div>
                </div>
              </div>
              {isLightboxOpen && (
                <Lightbox
                  mainSrc={imagesArray[currentImageIndex]}
                  nextSrc={
                    imagesArray[(currentImageIndex + 1) % imagesArray.length]
                  }
                  prevSrc={
                    imagesArray[
                      (currentImageIndex + imagesArray.length - 1) %
                        imagesArray.length
                    ]
                  }
                  onCloseRequest={handleLightboxClose}
                  onMovePrevRequest={() =>
                    setCurrentImageIndex(
                      (currentImageIndex + imagesArray.length - 1) %
                        imagesArray.length
                    )
                  }
                  onMoveNextRequest={() =>
                    setCurrentImageIndex(
                      (currentImageIndex + 1) % imagesArray.length
                    )
                  }
                  animationOnKeyInput={true} // Enable sliding animations with keyboard shortcuts
                />
              )}
              <br></br>
            </Item>
          </Grid>
        </Grid>{" "}
        <br></br>
        <Divider style={{ width: "100%" }} />
        <br></br>
      </div>
    </>
  );
};
export default React.memo(ProjectCardDetail);
